nav {
    width: $widthNavigation;
    padding: 0 $PaddingNavigation;
    margin: 0 auto;
    display: flex;
    align-items: center;
    background-color: $bgColorNavigation;
    height: $heightNavigation;
    
    .logo {
        display: flex;
        align-items: center;
        img {
            height: 50px;
            width: 50px;
        }
        p {
            color: $mainColor;
            font-family: $threFont;
            padding-left: 10px;
        }
    }
    .listNav {
        ul {
            display: flex;
            margin-left: 0px;
            position: relative;
            left: -15px;
            li {
                margin-left: 40px;
                display: block;
                position: relative;
                &:after {
                    content: " ";
                    width: 5px;
                    height: 5px;
                    border-radius: 50%;
                    position: absolute;
                    top: 5px;
                    right: -23px;
                    background-color: $mainColor;
                }
                &:last-child:after {
                    display: none;
                }
                a {
                    color: #ffffff;
                    font-family: $secondFont;
                    font-weight: 300;
                    letter-spacing: 0.5px;
                    &:hover {
                        opacity: 0.6;
                    }
                }
            }
        }
    }
}