#contentWrap {
    .blockWorks {
        width: 100%;
        padding: 50px 0;
        &__block {
            display: flex;
            width: 100%;
            padding: 10px 0;
            margin-bottom: 50px;
            font-family: $threFont;
            @include mobile {
                flex-direction: column;
            }
            &:hover a {
                transition-property: opacity;
                transition-duration: 1s;
                opacity: 1;
            }
        }
        &__img {
            position: relative;
            width: 400px;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            @include mobile {
                width: 100%;
            }
            img {
                width: 100%;
                height: 100%;
            }
            a {
                position: absolute;
                width: 150px;
                height: 50px;
                background-color: green;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $bgBottomOpenSite;
                color: $colorBottomOpenSite;
                text-transform: uppercase;
                font-family: $threFont;
                transition-property: opacity;
                transition-duration: 1s;
                opacity: 0;
                &:hover {
                    background-color: $bgBottomOpenSiteHover;
                }
            }
        }
        &__des {
            color: #ed6002;
            h2 {
                font-size: 15px;
                padding: 10px 0 0 10px;
                span {
                    font-weight: 700;
                    display: block;
                    padding-bottom: 4px;
                    color: $mainColor;
                }
            }
            p {
                font-size: 15px;
                padding: 10px 0 0 10px;
                line-height: 20px;
                span {
                    font-weight: 700;
                    display: block;
                    color: $mainColor;
                    padding-bottom: 4px;
                }
            }
        }
    }
}